@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

html {
	font-size: 62.5%;

	// блокировка скролла
	&.css-scroll-fixed:not(.open-only-mob) {
		@include scroll-blocked; }

	&.css-scroll-fixed.open-only-mob {
		@include media('<tab-lg') {
			@include scroll-blocked; } } }

/* === LAYOUT === */
.max-wrap {
	max-width: #{$page-layout-width +  nth($page-layout-space, 2)*2}; }

.layout {
	@include padding-horizontal(nth($page-layout-space, 2));
	@include push-auto; }

// выравнивание текстовых элементов на странице статьи (контент обернут в .layout-article)
// и их максимальная ширина
.layout-article {
	padding: $text-layout-space;

	@include media('<tab-lg') {
		padding: $text-layout-space--md; }

	@include media('<tab-sm') {
		padding: $text-layout-space--sm; }

	& > p, & > ul, & > ol, & > h1, & > h2, & > h3, & > h4, & > h5, & > h6, & > hr, & > blockquote, & > .layout-text, .imgc figcaption {
		max-width: $text-layout-width;
		@include padding-horizontal(0);
		@include push-auto; }

	.layout-flood {
		max-width: $flood-layout-width;
		@include padding-horizontal(0);
		@include push-auto; }

	// элеметы без отступов по бокам
	// минусовые маржины по горизонтале имеют значение только внутри .layout-article
	.layout-full-width, .layout-wide {

		@include margin-horizontal(-#{nth($text-layout-space, 2)});

		@include media('<tab-lg') {
			@include margin-horizontal(-#{nth($text-layout-space--md, 2)}); }

		@include media('<tab-sm') {
			@include margin-horizontal(-#{nth($text-layout-space--sm, 2)}); } } }

// максимальная ширина текстовых элементов на внутренней (текстовой) странице
.max-wrap-text {
	//max-width: #{$text-layout-width + (nth($text-layout-space, 2))*2}
	max-width: $text-layout-width;

	//+media('<tab-lg')
	//ax-width: #{$text-layout-width + (nth($text-layout-space--md, 2))*2}

	//+media('<tab-sm')
 }	//ax-width: #{$text-layout-width + (nth($text-layout-space--sm, 2))*2}

// выравнивание текстовых элементов на внутренней (текстовой) странице
// и их максимальная ширина
.layout-text {
	max-width: #{$text-layout-width + (nth($text-layout-space, 2))*2};
	@include padding-horizontal(nth($text-layout-space, 2));
	@include push-auto;

	@include media('<tab-lg') {
		max-width: #{$text-layout-width + (nth($text-layout-space--md, 2))*2};
		@include padding-horizontal(nth($text-layout-space--md, 2)); }

	@include media('<tab-sm') {
		max-width: #{$text-layout-width + (nth($text-layout-space--sm, 2))*2};
		@include padding-horizontal(nth($text-layout-space--sm, 2)); } }

// максимальная ширина альтернативных элементов на внутренней (текстовой) странице
.max-wrap-flood {
	//max-width: #{$flood-layout-width + (nth($text-layout-space, 2))*2}
	max-width: $flood-layout-width;

	//+media('<tab-lg')
	//ax-width: #{$flood-layout-width + (nth($text-layout-space--md, 2))*2}

	//+media('<tab-sm')
 }	//ax-width: #{$flood-layout-width + (nth($text-layout-space--mob, 2))*2}

// выравнивание альтернативных элементов на внутренней (текстовой) странице
// и их максимальная ширина
.layout-flood {
	max-width: #{$flood-layout-width + (nth($text-layout-space, 2))*2};
	@include padding-horizontal((nth($text-layout-space, 2)));
	@include push-auto;

	@include media('<tab-lg') {
		max-width: #{$flood-layout-width + (nth($text-layout-space--md, 2))*2};
		@include padding-horizontal((nth($text-layout-space--md, 2))); }

	@include media('<tab-sm') {
		max-width: #{$flood-layout-width + (nth($text-layout-space--sm, 2))*2};
		@include padding-horizontal((nth($text-layout-space--sm, 2))); }

	p, ul, ol, h1, h2, h3, h4, h5, h6, hr {
		max-width: none; } }

// элеметы с отступами по бокам
.layout-wide {
	@include padding-horizontal((nth($text-layout-space, 2)));

	@include media('<tab-lg') {
		@include padding-horizontal((nth($text-layout-space--md, 2))); }

	@include media('<tab-sm') {
		@include padding-horizontal((nth($text-layout-space--sm, 2))); }

	p, ul, ol, h1, h2, h3, h4, h5, h6, hr {
		max-width: none; } }

// элеметы без отступов по бокам
.layout-full-width {

	p, ul, ol, h1, h2, h3, h4, h5, h6, hr {
		max-width: none; } }

// выравнивание по центру блочных элементов
.push-auto {
	margin-left: auto;
	margin-right: auto; }

// лэяут страницы
.wrapper {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: stretch;
	margin: 0 auto;
	//min-height: 100vh
	//min-height: 100% // not support ie
	//height: 100%
	//overflow: hidden
	//height: 100vh
	min-height: 100vh;
	position: relative; }

/* === LAYOUT end === */

/* === CONTENT === */
.main {
	flex-shrink: 1;
	flex-grow: 1;
	position: relative;
	clear: both;
	z-index: 4; }

.main__holder {
	width: 100%;
	@include clear; }

.content-wrap {
	float: left;
	width: 100%; }

.content {
	margin: 0 200px 0 300px;
	min-height: 50px;

	background-color: whitesmoke;

	@include media('<desk-lg') {
		margin-right: 0; }

	@include media('<tab-lg') {
		margin-left: 0; } }

/* === CONTENT end === */

/* === HEADER === */
.header {
	background-color: lightgreen; }

.header__holder {
	position: relative;
	padding: 10px 0;
	z-index: 5;
	@include clear; }

/* logo */
.logo {
	position: relative;
	float: left;
	background: url(../../img/logo.png) no-repeat;
	width: 220px;
	height: 85px;
	text-indent: -9999px;
	overflow: hidden;
	margin: 0 2rem 0 0;
	z-index: 5;

	a, & > span {
		display: block;
		height: 100%; } }

/* logo end */
/* === HEADER end === */

/* === NAVIGATION === */
.nav {
	position: relative; }

.nav__list {
	@include clear;

	li {
		position: relative;

		&.has-drop {
			padding-right: 2rem;

			.nav__angle {
				position: absolute;
				right: 0;
				top: 50%;
				transform: translate(0, -50%);
				line-height: 0;
				pointer-events: none; } } }

	a {
		display: block; }

	& > li {
		float: left;
		margin-right: 2rem;

		&.has-drop > .nav__tab .nav__angle i {
			display: inline-block;
			vertical-align: top;
			@include triangle(bottom, tomato, 5px, 2); } } }

.nav__drop {
	position: absolute;
	left: 0;
	top: 100%;
	background-color: white;
	border: 1px solid $border-color;
	padding: 2rem;
	width: $page-layout-min;
	opacity: 0;
	visibility: hidden;
	z-index: 10;

	li:hover > & {
		opacity: 1;
		visibility: visible; }

	li {
		margin-bottom: 1rem;
		font-size: 1.4rem;
		line-height: 1.5;

		&.has-drop > .nav__tab .nav__angle i {
			display: inline-block;
			vertical-align: top;
			@include triangle(right, tomato, 5px, 2); } }


	.nav__drop {
		top: 0;
		left: 100%; } }

/* === NAVIGATION end === */

/* === SIDEBAR === */
.sidebar {
	display: inline;
	float: left;
	margin: 0 0 0 -100%;
	width: $sidebar-width;

	background-color: sandybrown;

	@include media('<tab-lg') {
		display: none; } }

/* === SIDEBAR end === */

/* === ASIDE === */
.aside {
	display: inline;
	float: right;
	margin: 0 0 0 -#{$aside-width};
	width: $aside-width;
	background-color: sandybrown;

	@include media('<desk-lg') {
		display: none; } }

/* === ASIDE end === */

/* === FOOTER === */
.footer {
	//margin: 0 auto
	position: relative;
	clear: both;
	z-index: 5;
	@include font(16px, round-float(24/16, 3));
	color: $text-color;
	border-top: 1px solid $border-color;

	background-color: lightgreen; }

.footer__holder {
	overflow: hidden;
	cursor: default;
	padding: $footer-grid-space; }

/* footer bottom */
.footer-bottom {
	position: relative;
	// обрезать вытянутый маржинами по горизонтале дочерний элемент
	overflow: hidden; }

.footer-bottom__holder {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	@include margin-horizontal(-#{round(nth($grid-space, 2)/2)});
	margin-bottom: -#{round(nth($grid-space, 1)/2)}; }

.footer-bottom__col {
	text-align: center;
	@include padding-horizontal(round(nth($grid-space, 2)/2));
	margin-bottom: #{round(nth($grid-space, 1)/2)};
	// для браузеров не поддерживающих display: flex
	display: inline-block;
	vertical-align: top;
	width: calc(33.33% - 5px);

	@include media('<mob-lg') {
		min-width: 100%; }

	&:first-child {
		@include media('>=mob-lg') {
			text-align: left; } }

	&:last-child {
		@include media('>=mob-lg') {
			text-align: right; } }

	a {
		color: inherit;
		text-decoration: none;

		.no-touchevents &:hover {
			text-decoration: underline; } } }

/* footer bottom end */

/* developer */
.developer {
	display: inline-block;

	a {
		text-decoration: none;

		.no-touchevents &:hover {
			text-decoration: none; } } }

.developer__label {
	@include media('<mob-lg') {
		display: block; }

	i {
		@include media('<mob-lg') {
			display: none; } } }

.developer__logo {
	@include display-inline-block-top;
	position: relative;
	top: 5px;
	padding-right: 3px;
	@include size(154px, 20px);
	text-align: left;
	text-indent: -9999px;

	&::before, &::after {
		@include pseudo();
		@include position(0, 0);
		@include size(149px, 16px); }

	&::before {
		@include pseudo();
		@include position(4px, 4px);
		background: url('data:image/png;base64,');
		background-size: 2px; }

	//.no-touchevents a:hover
	//ransform: translate(0, 1px)
	//ilter: blur(2px)
	//pacity: 0.6

	&::after {
		@include pseudo();
		background: #00ef85 url('data:image/png;base64,') no-repeat center center;
		background-size: auto 16px;

		.no-touchevents a:hover & {
			transform: translate(0, -1px); } } }

/* developer end */
/* === FOOTER end === */
